.page{
  background: $c-gray;
  margin: 0;
  min-width: 320px;
  width: 100%;


    &__menu {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      transition: all 0.3s;
      opacity: 0;
      transform: translateX(-100%);
      pointer-events: none;
      z-index: 10;

      &:target {
        opacity: 1;
        transform: translateX(0);
        pointer-events: all;
      }
    }

    &:has(.page__menu:target) {
      overflow: hidden;
    }
}
